$(".qa-card__show").click(function () {
    $(this).parents(".qa-card").toggleClass("_active")
    $(this).siblings(".qa-card__hidden").slideToggle()
})

$("._scroll-to-calc").click(function (e) {
    if(window.innerWidth<=576){
        e.preventDefault()
        showCalc()
    }
})

$("._show-calc").click(function () {
    showCalc()
})

function showCalc(){
    $(".calc").addClass("_open")
    $("body").addClass("fixed-body")
}

$(".calc__close").click(function () {
    $(".calc").removeClass("_open")
    $("body").removeClass("fixed-body")
})

$(".sticky-menu__close").click(function () {
    $(".sticky-menu").hide()
})

function getHeaderHeight(){
    return document.querySelector(".header").getBoundingClientRect().height
}

$(".sticky-menu__item").click(function(event) {
    event.preventDefault();
    var idc = $(this).attr('href'),
        top = $(idc).offset().top;
    $('body,html').animate({ scrollTop: top}, 500);
});
$(window).scroll(function() {
    var $sections = $('._anchor');
    $sections.each(function(i, el) {
        var top = $(el).offset().top;
        var bottom = top + $(el).height();
        var scroll = $(window).scrollTop() + 2 * getHeaderHeight();
        var idn = $(el).attr('id');
        if (scroll > top  && scroll < bottom) {
            $('a.sticky-menu__item').removeClass('_active');
            $('a[href="#' + idn + '"]').addClass('_active');
        }
    });
});
