$("._show-catalog").click(function () {
    if(window.innerWidth>992){
        $(".header-catalog").addClass("_active")
        $("body").addClass("fixed-body")
    }else{
        $(this).toggleClass("_active")
        $(".header-catalog").slideToggle()
    }
})

$("._hide-catalog").click(function () {
    $(".header-catalog").removeClass("_active")
    $("body").removeClass("fixed-body")
})

$(".menu__top").click(function () {
    $(this).toggleClass("_active")
    $(this).siblings(".menu__bottom").slideToggle()
})


$(".burger").click(function () {
    $(".header").addClass("_open")
    $("body").addClass("fixed-body")
})

$(".mob-menu__close").click(function () {
    $(".header").removeClass("_open")
    $("body").removeClass("fixed-body")
})
