function calcSliders() {
    let calcSlidersArray = []
    $(".calc-group").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        calcSlidersArray[index] = new Swiper(`.calc-group[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            navigation: {
                nextEl: `.calc-group[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.calc-group[data-slider-id="${index}"] .swiper-button-prev`,
            },
        })
    })
}

function solutionsSlider() {
    var swiper = new Swiper('.solutions-slider.swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        observer: true,
        observeParents: true,
        observeSlideChildren: true,
        navigation: {
            nextEl: '.solutions__nav .swiper-button-next',
            prevEl: '.solutions__nav .swiper-button-prev',
        },
        pagination: {
            el: '.swiper-pagination.solutions-slider__pag',
            type: 'bullets',
            clickable: true,
        },
    })
}



