const ranges = document.querySelectorAll(`.calc-range`)

ranges.forEach(range => {
    const rangeSlider = range.querySelector(`.range`)
    const arbitraryValuesForSlider = [1,50,100,150,200];

    let format = {
        to: function(value) {
            return arbitraryValuesForSlider[Math.round(value)];
        },
        from: function (value) {
            return arbitraryValuesForSlider.indexOf(value);
        }
    };

    if (rangeSlider) {
        noUiSlider.create(rangeSlider, {
            start: 61,
            animate: false,
            connect: [true, false],
            range: {
                min: 0,
                max: 200
            },
            step: 1,
            pips: { mode: 'count', values: 5 },
        });
    }
    if (rangeSlider) {
        rangeSlider.noUiSlider.on('update', function(values, handle) {
            range.querySelector('.calc-range__num').innerHTML = Math.round(values[handle])
        })
    }
})


cartCalc()

function cartCalc(){
    $('.cartcalc .ccalc-minus').click(function(){
        let a = $(this).closest('.cartcalc').find('input').val();
        if(a>1){
            let b = +a-1;
            $(this).closest('.cartcalc').find('input').val(b);
        }else{
            $(this).closest('.cartcalc').find('input').val(a);
        }
    });
    $('.cartcalc .ccalc-plus').click(function(){
        let a = $(this).closest('.cartcalc').find('input').val();
        let b = +a+1;
        $(this).closest('.cartcalc').find('input').val(b);
    });
}
